<template>
  <b-row>
    <b-col cols="12" xl="12">
      <transition name="slide">
      <b-card :header="caption">
        <b-table :hover="hover" :striped="striped" :bordered="bordered" :small="small" :fixed="fixed" responsive="sm" :items="items" :fields="fields" :current-page="currentPage" :per-page="perPage" @row-clicked="rowClicked">
          <template slot="retrato" slot-scope="data">
            <b-navbar variant="faded" type="light">
              <b-navbar-brand tag="h1" href="#">
                <img :src="data.item.photo" width="40px" class="img-avatar d-inline-block align-top" alt="BV">
              </b-navbar-brand>
            </b-navbar>

          </template>
          <template slot="exp" slot-scope="data">
            <strong>k-18-0071-0089{{data.item.id}}</strong>
          </template>
          <template slot="dibujante" slot-scope="data">
            <strong>{{data.item.name}}</strong>
          </template>
          <template slot="usuario" slot-scope="data">
            <strong>{{data.item.username}}{{getUsername(data.item.name)}}</strong>
          </template>
          <template slot="fecha" slot-scope="data">
            <b-badge >{{data.item.registered}}</b-badge>
          </template>
          <template slot="solicitud" slot-scope="data">
            <strong>{{data.item.request}}</strong>
          </template>
          <template slot="acci√≥n" slot-scope="data">
            <b-button variant="primary" class="btn-pill">Detalles</b-button>
            <b-button variant="success" class="btn-pill">Actualizar</b-button>
          </template>
        </b-table>
        <nav>
          <b-pagination size="sm" :total-rows="getRowCount(items)" :per-page="perPage" v-model="currentPage" prev-text="Prev" next-text="Next" hide-goto-end-buttons/>
        </nav>
      </b-card>
      </transition>
    </b-col>
  </b-row>
</template>

<script>
import actData from './ActData'

export default {
  name: 'Usuarios',
  props: {
    caption: {
      type: String,
      default: 'Users'
    },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  data: () => {
    return {
      items: actData.filter((user) => user.id < 42),
      fields: [
        {key: 'retrato'},
        {key: 'exp'},
        {key: 'dibujante'},
        {key: 'fecha'},
        {key: 'solicitud'},
        {key: 'acci√≥n'}
      ],
      currentPage: 1,
      perPage: 15,
      totalRows: 0
    }
  },
  computed: {
  },
  methods: {
    getBadge (status) {
      return status === 'Active' ? 'success'
        : status === 'Inactive' ? 'secondary'
          : status === 'Pending' ? 'warning'
            : status === 'Banned' ? 'danger' : 'primary'
    },
    getRowCount (items) {
      return items.length
    },
    userLink (id) {
      return `users/${id.toString()}`
    },
    rowClicked (item) {
      const userLink = this.userLink(item.id)
      this.$router.push({path: userLink})
    },
    getUsername(name){
      return name.split(' ')[0]
    }

  }
}
</script>

<style scoped>
.card-body >>> table > tbody > tr > td {
  cursor: pointer;
}
</style>
